import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VChanggeMoneyService, VStringService, Helper, VPreviewInvService, sEnum } from '@core/index';
import { Library } from '@core/library';
import { AuthService, SocketService } from '@core/services';
import { HandlebillService } from '@modules/handle-bills/shared/handlebill.service';
import { TemplateService } from '@modules/template/shared';
import { TranslateService } from '@ngx-translate/core';
import { SendDataForTaxComponent, ViewInvoiceMultipageComponent } from '@shared/components';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
import { BillsCancelComponent } from '../bills-cancel/bills-cancel.component';
import { ReportTaxBillErrorComponent } from '../report-tax-bill-error/report-tax-bill-error.component';
import { Subscription } from 'rxjs/Subscription';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { HandleInvoiceService } from '@modules/handle-invoice/shared';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { InvoiceService } from '@shared/service/invoice.service';
import { DxDataGridComponent } from 'devextreme-angular';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  dataSource: any;
  selectedRows: Array<any> = [];
  pageSelect: any;
  _translate: any;
  _language: any;
  bsModalSignRef: BsModalRef;
  configs: any;
  isTT78: any;
  valueProgress: any
  showPopup = false;
  bsModalRef: BsModalRef;
  paramSearch: any;
  format: any;
  infoDangKy: any;
  selectedItems: Array<any> = [];
  isFormHandInvoice: any;
  dataInfor: any;
  formatter: any;
  objStatusBtn = {};
  checkSubmit: any;
  ioSignature: Subscription;
  isDisableAdjust: false;
  isDisableReplace: false;
  currentUser: any;
  dsTinhTrangXuLy = [];
  dsMauSoKyHieu = [];
  listWrongStatus = [];
  constructor(
    private authService: AuthService,
    private modalService: BsModalService,
    private router: Router,
    private templateService: TemplateService,
    public translate: TranslateService,
    private handlebillService: HandlebillService,
    private billService: BillService,
    private vPreviewInvService: ViewInvoiceService,
    private invoiceService: InvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);

  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.layDsMauSoKyHieu();
    this.dsTinhTrangXuLy = VSelectBoxService._adjustmentTypes(this._translate);
    this.listWrongStatus = VSelectBoxService._listWrongStatus(this._translate);
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.currentUser = this.authService.getCurrentUser();
    this.configs = this.authService.getConfigs();
    if (this.currentUser.is_convert_TT78) {
      this.infoDangKy = this.authService.getInfoRegister();
    }
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
  }

  deleteItem(data) {
    let ids = data.id;
    const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.billService.delete(ids).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.delete_success, 'success');
            this.loadList(this.paramSearch);
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }


  editNotSelect(row) {
    if (row.status != 'GHI_TAM') {
      Library.notify('Bn khng th sa ha n  xut', 'error');
    } else {
      if (row.adjustment_type === '5') {
        this.router.navigate([`/system/handle-bills/edit-adjust/${row.id}`]);
      } else if (row.adjustment_type === '3') {
        this.router.navigate([`/system/handle-bills/edit-replace/${row.id}`]);

      }
    }
  }

  onRowPrepared(e) {
    if (e.rowType == 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.bill_number) {
            if (e.data.send_department_status == "DA_GUI_CQT")
              e.rowElement.style.color = '#008B8B';
            else if (e.data.send_department_status == "DA_CAP")
              e.rowElement.style.color = 'black';
            else if (e.data.send_department_status == "DA_TIEP_NHAN")
              e.rowElement.style.color = 'black';
            else
              e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        let eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }

  loadList(params?) {
    this.dataSource = {};
    this.dataSource.store = this.handlebillService.getAll(params);
  }

  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }

  displayFunction = (item) => {
    if (!item) {
      return 'Tt c';
    }
    return item.name;
  }


  layDsMauSoKyHieu() {
    let template = this.templateService.getAll1().subscribe(
      (rs) => {
        if (rs.status == 'success') {
          this.dsMauSoKyHieu = rs.data.templates.filter((item) => (item.template_type == sEnum.templatetype.HoaDonKhoiTaoTumayTinhTien || item.template_type == sEnum.templatetype.HoaDonMayTinhTien));
          this.dsMauSoKyHieu = this.dsMauSoKyHieu.map(item => {
            let { template_code, invoice_series } = item;
            return {
              code: `${template_code}-${invoice_series}`,
              name: `${template_code}${invoice_series}`
            }
          })

        }
        else {
          Library.notify(rs.message, 'error')
        }
      },
      (err) => { },
      () => {
        template.unsubscribe();
      }
    )
  }

  // Hu ho n
  delete() {
    let ids = [];
    let lstInvoice = this.selectedItems.filter(p => p.adjustment_type == '1');
    lstInvoice = lstInvoice.sort((invA, invB) => {
      return <any>new Date(invB.bill_date) - <any>new Date(invA.bill_date) || invB.bill_number.localeCompare(invA.bill_number);
    });
    ids = lstInvoice.map(e => e.id);
    Library.showloading();
    let isView = false;
    if (ids.length > 0) {
      const sub = this.handlebillService.showHDXoaBo(ids, isView).subscribe(rs => {
        let lstInvoice = rs[0].data.bills.filter(p => p.adjustment_type == '7');
        if (lstInvoice && lstInvoice.length > 0)
          isView = true;
        let lstInv = [];
        ids.forEach(id => {
          let inv = rs[0].data.bills.filter(p => p.id == id)[0];
          lstInv.push(inv);
        });
        const initialState = {
          isView: isView,
          data: lstInv,
          configs: this.configs,
          infoDangKy: this.infoDangKy

        };
        this.bsModalRef = this.modalService.show(BillsCancelComponent, {
          class: 'modal-lg full-width', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          this.selectedRows = [];
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    } else {
      Library.hideloading();
      Library.notify('Khng th hy ha n ny', 'error');
    }
  }


  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems;
      let is_default_tempplate = true;
      if (_lst.length > 0 || data) {
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
          if (!VStringService._attemptConvertBool(data.is_default_template))
            is_default_tempplate = false;
        } else {
          ids = _lst.map(e => e.id);
        }
        if (is_default_tempplate) {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_hoa_don_mtt_html
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
        else {
          if (this.configs.system_parameter.view_multi_invoice) {
            let data = this.billService.taodulieuXemMau(ids);
            data['configs'] = sEnum.cauhinhxemmau.hoadon;
            this.vPreviewInvService.xemMau(data);
          }
          else {
            const initialState = {
              items: ids,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.xem_hoa_don_mtt,
              cssClass: 'modal-xl view-bill'
            };
            this.vPreviewInvService.xemMauPhanTrang(initialState);
          }
        }

      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  selectionChanged(data) {
    this.selectedItems = data.selectedRowsData;
  }

  onImportShowing(e) {

  }
  onImportHiding() {

  }

  BaoCaoCQT() {
    Library.showloading();
    let sub = this.templateService.getAll1().subscribe(rs => {
      if (rs.status == 'success') {
        let listTemplate = rs.data.templates;
        let lstInvoice = this.selectedItems;
        lstInvoice = lstInvoice.sort((invA, invB) => {
          return <any>new Date(invB.bill_date) - <any>new Date(invA.bill_date) || invB.bill_number.localeCompare(invA.bill_number);
        });
        const initialState = {
          data: lstInvoice,
          configs: this.configs,
          listTemplate: listTemplate,
          infoDangKy: this.infoDangKy
        };
        this.bsModalRef = this.modalService.show(ReportTaxBillErrorComponent, {
          class: 'modal-lg full-width', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          this.selectedRows = [];
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      }
      else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  ThongDiepGuiNhan(data) {
    Library.showloading();
    let lstErr = data.bill_wrongs;
    const initialState = {
      lstErr: lstErr,
      notifyType: "THONG_DIEP_TRUYEN_NHAN_MTT",
      send_wrong_status: data.send_wrong_status,
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'modal-lg truyen-nhan',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
    Library.hideloading();
  }

  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }

  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  ngOnDestroy() {
  }

  thayThe() {
    if (this.selectedItems) {
      if (this.selectedItems.length == 1) {
        const row = this.selectedItems[0];

        //hd t phn mm
        if (row.status == sEnum.trangthaiCQT.DA_TIEP_NHAN && row.adjustment_type && (row.adjustment_type == 1 || row.adjustment_type == 3)) {
          this.router.navigate(['/system/handle-bills/replace/' + row.id])
        } else {
          Library.notify('Khng th lp ha n thay th cho ha n ny', 'error');
        }

      }
      else {
        Library.notify('Vui lng ch chn 1 ho n.', 'error')
      }

    }
  }

  dieuChinh() {
    if (this.selectedItems) {
      if (this.selectedItems.length == 1) {
        const row = this.selectedItems[0];
        //hd t phn mm
        if (row.adjustment_type && (row.adjustment_type == 1 || row.adjustment_type == 4)) {
          this.router.navigate(['/system/handle-bills/adjust/' + row.id])
        } else {
          Library.notify('Khng th lp ha n iu chnh cho ha n ny', 'error');
        }
      }
      else {
        Library.notify('Vui lng ch chn 1 ho n.', 'error')
      }

    }
  }


  calculateFilterExpression(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains";
    return [["customer_name_temp", selectedFilterOperation || '=', filterValue], 'or', ["buyer_name_temp", selectedFilterOperation || '=', filterValue]];
  }
  calculateFilterExpressionMS(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains";
    let bill_series = filterValue;
    let template_code = filterValue;
    if (filterValue) {
      let arrStr = filterValue.split('-');
      template_code = arrStr[0].trim();
      bill_series = filterValue.substring(filterValue.indexOf(template_code) + 2).trim();
    }
    return [["bill_series", selectedFilterOperation || '=', bill_series], 'and', ["template_code", selectedFilterOperation || '=', template_code]];
  }
}
