import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { pattern } from '@config/pattern';
import { Helper, Library, VChanggeMoneyService, VPreviewInvService, VStringService, sEnum } from '@core/index';
import { AuthService, SocketService } from '@core/services';
import { ConfigsService } from '@modules/configs/shared';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmService } from '@shared/service/confirm.service';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import * as moment from 'moment';
import * as _ from 'lodash';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { Subscription, Observable } from 'rxjs';
import { BillModel } from '@modules/bill-invoice/shared/bill.model';
import { CanComponentDeactivate } from '@modules/vat/deactivate/vat-deactivate.guard';
import { NgxSpinnerService } from 'ngx-spinner';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { HandlebillService } from '@modules/handle-bills/shared/handlebill.service';
import { NotifyReduceTaxComponent } from '@shared/index';
import { InvoicesService } from '@core/library/invoice/invoices.service';
import { String } from 'typescript-string-operations';
import { AdjustProductGripComponent } from '../adjust-product-grip/adjust-product-grip.component';
import { AdjustFormBillComponent } from '../adjust-form-bill/adjust-form-bill.component';
import { PreviewInvoiceComponent } from '@shared/components/preview-invoice/preview-invoice.component';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { MyBsModalService } from '@core/library/invoice/my-bsmodel.service';

@Component({
  selector: 'app-adjust-bill',
  templateUrl: './adjust-bill.component.html',
  styleUrls: ['./adjust-bill.component.scss']
})
export class AdjustBillComponent implements OnInit, OnDestroy, AfterViewInit {

  @ViewChild(AdjustProductGripComponent) productGrip: AdjustProductGripComponent;
  @ViewChild(AdjustFormBillComponent) formInvoice: AdjustFormBillComponent;

  verify_code: any;
  info_message: any;
  bsModalRef: BsModalRef;
  bsModalHdckRef: BsModalRef;
  ioSignature: Subscription;
  routeSub: Subscription;
  isShowKyLenhDieuDong: boolean;
  isShowHuyLenhDD: boolean = false;
  isCheckLenhDieuDong = false;
  jsonProductFirst: any
  frmData: BillModel = new BillModel(5);
  oldForm: any;
  total_payment: any;
  ttDaXuatHoaDon = false; // Trng thi  xut ha n hay cha
  customer_codes: any;
  is_multi_vat: any;
  bill_products: ArrayStore;
  dataSource: DataSource;
  formatter: any = {};
  select: any = {};
  bill_products_old: any = [];
  formatNumber = Helper.numberInvoiceFormatDev;
  windowPreviewBeforeSubmit: any;
  confirmCheckPreview = false;
  isHdGTGT: boolean = true;
  configs: any;
  _translate: any;
  isAccessExportInv = false; //c quyn ph duyt?
  valueChange: string;
  nodeSign: any;
  productCodePattern: any = pattern.productCodePattern;
  _oTemplateTT78: any = {};
  infoDangKy: any;
  isDisplayGuideSale: boolean = false;
  bills: any = []; //c th dng khi gi mail sai st(test li sau)
  isExportInvoice: boolean; //trng thi xut ho n.  xut hay cha ?
  invoiceTemplateSelect: any = {};
  constructor(
    private route: ActivatedRoute,
    private billService: BillService,
    private authService: AuthService,
    private cdRef: ChangeDetectorRef,
    public translate: TranslateService,
    private confirmService: ConfirmService,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private modalService: BsModalService,
    private handleBillService: HandlebillService,
    private configsService: ConfigsService,
    private vPreviewInvService: ViewInvoiceService,
    private customeModalService: MyBsModalService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }
  ngOnDestroy(): void {
    this.ioSignature.unsubscribe();
    this.routeSub.unsubscribe();
  }

  //c gi khi thot khi mn ny v chuyn ti mn khc
  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    if (!this.checkDataFormChange(this.valueChange, this.frmData)) {
      return new Observable<boolean>(
        (observer) => {
          this.confirmService.confirm().subscribe(
            value => {
              switch (value) {
                case 'yes':
                  this.ghiTam().then((value1) => {
                    observer.next(value1);
                    observer.complete();
                  });
                  break;
                case 'no':
                  observer.next(true);
                  observer.complete();
                  break;
                case 'cancel':
                  observer.next(false);
                  observer.complete();
                  break;
                default:
                  observer.next(true);
                  observer.complete();
                  break;
              }
            }
          );
        });
    }
    return true;
  }

  ngOnInit() {

    // kt ni websocket
    this.connectWebsocket();
    let currentUser = this.authService.getCurrentUser();
    let listpermissions = currentUser.user.permissions.split(',');
    this.configs = this.authService.getConfigs();
    this.infoDangKy = this.authService.getInfoRegister();
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
    this.formatter.display_int = this.configs.system_parameter.display_int;
    this.routeSub = this.route.data.subscribe((data: any) => {
      // put data danh mc for select
      this.select.unit_codes = data.init[1].DM_DON_VI_TINH;
      this.select.select_provisions_on_accounts = data.init[1].DM_DINH_KHOAN;
      this.select.payment_method_names = data.init[1].DM_HINH_THUC_THANH_TOAN;
      this.select.vats = data.init[1].DM_TY_LE_VAT;
      this.select.templatesTT78 = data.init[0];
      // if (temptTT78 && temptTT78.length > 0) {
      //   this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.infoDangKy.is_invoice_with_code);
      //   if (data.init[2] && Number.parseInt(data.init[2].invoice_number) > 0) {
      //     if (data.init[2].is_invoice_with_code != this.infoDangKy.is_invoice_with_code && data.init[2].xml_type != '1') {
      //       this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == data.init[2].is_invoice_with_code);
      //     }
      //   }
      // }
      this.select.templatesTT78 = this.select.templatesTT78.filter(p => (p.status == "HOAT_DONG" && p.template_code == data.init[2].template_code));
      if (data.status === 'thaythemoi') {
        this.khoiTaoKhiThemMoi(data.init[2]);
        this.oldForm = data.init[2];
        this.frmData.destroy_reason = "";
      } else {
        this.khoiTaoKhiSua(data.init[2]);
        this.oldForm = data.init[2].origin;
      }
      // sx sp
      this.oldForm.bill_products = _.sortBy(this.oldForm.bill_products, item => parseInt(item.order));
      if(this.oldForm.origin) {
        this.oldForm.origin.bill_products = _.sortBy(this.oldForm.origin.bill_products, item => parseInt(item.order));
      }
      if (this.oldForm.template_code == '1') {
        this.isHdGTGT = true;
      } else {
        this.isHdGTGT = false;
      }
      // sx gim dn 
      this.select.templatesTT78.sort((a, b) => {
        const dateA: any = new Date(a.created_at);
        const dateB: any = new Date(b.created_at);
        return dateB - dateA;
      });
      if (this.select.templatesTT78.length > 0) {
        this._oTemplateTT78 = _.cloneDeep(this.select.templatesTT78[0]);
      }
      this.select.templatesTT78.some(item => {
        if (item.value === this.frmData.template_id) {
          this.is_multi_vat = item.is_multi_vat;
          this.invoiceTemplateSelect = item;
          this._oTemplateTT78 = _.cloneDeep(item);
          return true;
        }
      });
     // this.frmData.template_id = this._oTemplateTT78.value;
      this.frmData.is_resolution_43 = false;
    });
    let obj2 = listpermissions.filter(p => p === 'phe_duyet');
    if (obj2.length > 0) {
      this.isAccessExportInv = true;
    } else {
      this.isAccessExportInv = false;
    }
  }

  khoiTaoKhiSua(data) {
    const inv_products = this.frmData.setInvoice_products_data(data.bill_products);
    // khi to li t d liu sa
    this.bill_products = new ArrayStore({
      key: 'id',
      data: inv_products
    });
    this.dataSource = new DataSource({
      store: this.bill_products
    });
    // khi to d liu form
    this.frmData.fillFormData(data);
    this.frmData.adjustment_type = 5;
    // cu hnh thp phn thm mi
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(data.decimal_places_json));
    // co cho sua luoi hang hoa ko
    let temp = this.frmData.adjustment_form + '_' + this.frmData.adjustment_object;
    this.productGrip.gripAllowEdit = true;
    if (temp === 'TANG_HH_DV' || temp === 'GIAM_HH_DV') {
      this.setContentProductNameV2('', 'select');
    }
  }

  khoiTaoKhiThemMoi(data) {
    // khi to mi li
    const inv_products = this.frmData.bill_products_data();
    // inv_products.forEach(item => {
    //   item.vat = item.vat != null ? item.vat.toString() : null;
    // });
    this.bill_products = new ArrayStore({
      key: 'id',
      data: inv_products
    });
    this.dataSource = new DataSource({
      store: this.bill_products
    });
    this.bill_products_old = data.bill_products;

    // khi to d liu form
    this.frmData.fillFormDieuChinh(data);
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(data.decimal_places_json));
  }

  ngAfterViewInit() {
    if (VStringService._getValueToString(this.frmData.status) != "" && this.frmData.status != sEnum.statusInv.GhiTam) {
      this.disablleScreen();
      this.cdRef.detectChanges();
    }
    this.setDataSessionStorage(this.frmData);
  }

  setDataSessionStorage(value) {
    this.valueChange = JSON.stringify(value);
  }

  disablleScreen() {
    this.formInvoice.readonlyAllForm();
    this.productGrip.ttDaXuatHoaDon = true;
    this.ttDaXuatHoaDon = true;
  }

  sendInvoice(xml_string) {
    setTimeout(function () {
      Library.showloading();
    }, 20);
    let xmlInvoice = "";
    const sub = this.billService.sendInvoice({
      id: this.frmData.id,
      xml_string: xml_string,
    }).subscribe(rs => {
      if (rs.status === 'success') {
        this.info_message = rs.data.info_message;
        let bill_sign_date = moment(rs.data.bill_sign_date).format('YYYY-MM-DD HH:mm:ss');
        if (this.info_message && this.info_message.message) {
          xmlInvoice = VStringService._getValueToString(this.info_message.message.message);
          if (xmlInvoice != "") {
            this.kyThongDiep(xmlInvoice, bill_sign_date);
          }
        }
      }
      else
        Library.notify(rs.message, 'error');
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.SvcName === 'efy_Sign') {
        if (data.Data.Name === 'hoadon.xml') {
          if (this.ttDaXuatHoaDon)
            this.sendData_TBSaiSot(data.Data.Content);
          else
            this.sendInvoice(data.Data.Content);
        } else if (data.Data.Name === 'thongdiep.xml') {
          this.goiApiXuatHoaDon(data.Data.Content);
        }
        else if (data.Data.Name === 'ten_filek_the_trung-dc-nhi.xml') {
          if (this.ttDaXuatHoaDon)
            this.getXml_TBSaiSot();
          else {
            this.xuLiApiKhiXuat();
          }
        }
      }
    });
  }

  eSubmit(type) {
    const checkValid = this.formInvoice.form.instance.validate();
    if (!checkValid.isValid) {
      return;
    }
    this.total_payment = "";
    if (this.windowPreviewBeforeSubmit) {
      this.windowPreviewBeforeSubmit.focus();
      return;
    }
    setTimeout(() => {
      this.handleFormData();
      delete this.frmData["note_view_on_bill"];
      if (type !== 'GHI_TAM') {
        if (!this._validForm()) {
          return;
        }
        if (!InvoicesService.checkTimeExportInv(this.configs.system_parameter)) {
          Library.notify(String.Format(this._translate.NOTIFY.time_export_inv, moment(this.configs.system_parameter.time_start).format('HH:mm'), moment(this.configs.system_parameter.time_end).format('HH:mm')), 'error');
          return;
        }
      }
      else {
        if (!this.checkProductCode()) {
          return;
        }
      }
      if (this.frmData.bill_options) {
        this.frmData.bill_options.forEach(item => {
          delete item['is_show'];
          delete item['type'];
          delete item['width'];
          delete item['statusData'];
          delete item['id'];
          delete item['invoice_id'];
        });
      }

      if (this.frmData.total_payment > 20000000 && (this.frmData.payment_method_name === 'TM')) {
        Library.notify('Vui lng chn HTTT chuyn khon cho ha n c gi tr ln hn 20 triu ng', 'error');
        return false;
      }
      if (!this.isAccessExportInv && type === 'XUAT') {
        Library.notify(this._translate.NOTIFY.you_not_permissions, 'error');
        return false;
      }
      if (type === 'XUAT') {
        this.processXuatHoaDon(type)
      } else {
        this.processGhiTam();
      }
    }, 300);
  }

  processXuatHoaDon(type) {
    let _index = 0;
    let _isValid = true;
    let _lstInv = [];
    this.bill_products.load().then((dataSource) => {
      _lstInv = dataSource.filter(p => p.product_name);
    });

    for (let i = 0; i < _lstInv.length; i++) {
      if (_lstInv[i].amount > 0 && !_lstInv[i].vat && !_lstInv[i].is_refund_ticket) {
        _index = i;
        _isValid = false;
        break;
      }
    }
    if (!this.isHdGTGT)
      _isValid = true;
    if (!this.billService.checkVatMauHoaDon(_lstInv, this.is_multi_vat)) {
      return false;
    }

    if (!_isValid) {
      const result = Library.notify('VAT khng hp l', 'error');
    } else {
      if (this.confirmCheckPreview) {
        this.xuatHoaDon();
        this.confirmCheckPreview = false;
      } else {
        this.previewBeforeExport(type);
        // if (this.configs.system_parameter.display_template_from_server)
        //   this.previewBeforeSubmit(type);
        // else
        //   this.previewBeforeSubmitFromClient(type);
      }
    }
  }
  previewBeforeExport(type) {
    this.handleFormData();
    let trang_thai = this._translate.THONG_TIN.xuat_hoa_don;
    let du_lieu = this.billService.taodulieuXemTruocMau(this.frmData);
    du_lieu['configs'] = sEnum.cauhinhxemmau.hoadon;
    let content_invoice = {
      du_lieu: du_lieu,
      ngay_hoa_don: "",
      trang_thai: trang_thai,
      tong_tien: "",
      title_invoice_type: this._translate.THONG_TIN.ban_du_thao_hoa_don.toUpperCase(),
      note_warn : this._translate.THONG_TIN.luu_y_truoc_khi_xuat,
      is_display_img_note: true,
      text_ngay_hoa_don: this._translate.THONG_TIN.ngay_hoa_don,
      text_tong_tien: this._translate.THONG_TIN.tong_tien,
      question_again: this._translate.THONG_TIN.ban_co_chac_chan_xuat_hoa_don_khong
    }
    const initialState = {
      content_invoice: content_invoice,
    };

    this.bsModalRef = this.customeModalService.show(PreviewInvoiceComponent, {
      backdrop: 'static',
      keyboard: false,
      class: 'modal-xl prv-s-invoice v-s-invoice',
      initialState
    });

    const subModal = this.customeModalService.onHide.subscribe(() => {
      if (this.bsModalRef.content.successSaveItem == sEnum.actionHander.DongY) {
        this.confirmCheckPreview = true;
        this.eSubmit(type);
      }
      subModal.unsubscribe();
    });
  }
  xuatHoaDon() {
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLiApiKhiXuat();
    } else {
      let ListSerialNumber = "DSCKS";
      if (this.infoDangKy && this.infoDangKy.registration_signature) {
        let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
        if (signatures && signatures.length > 0)
          ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
      }
      let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
      this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
    }
  }
  /* X l ghi tm ha n */
  processGhiTam() {
    this.ghiTam();
  }

  async ghiTam() {
    this.productGrip._tinhGiaTri();
    if (!this.frmData.id) {
      await this.billService.addNew(this.frmData, false).subscribe(res => {
        if (res.status === 'success') {
          this.frmData.id = res.data.id;
          // this.isDisable = true;
          Library.notify('Ghi tm ha n thnh cng', 'success')
          return true;
        } else {
          Library.notify(res.message, 'error')
          return false;
        }
      });
    }
    else {
      await this.billService.edit(this.frmData.id, this.frmData, false).subscribe(res => {
        if (res.status === 'success') {
          this.frmData.id = res.data.id;
          Library.notify('Ghi tm ha n thnh cng', 'success');
          return true;
        } else {
          Library.notify(res.message, 'error')
          return false;
        }
      });
    }
    return true
  }

  /* Kim tra M hng phi nhp khng du, khng cha k t c bit v ti a 50 k t */
  checkProductCode() {
    let isValid = true;
    const _lst = this.frmData.bill_products.filter(p => p.product_name);
    if (_lst.length > 0) {
      for (let i = 0; i < _lst.length; i++) {
        if (VStringService._getValueToString(_lst[i].product_code) != "" && !this.productCodePattern.test(_lst[i].product_code)) {
          Library.notify(this._translate.VALIDATION.required_productCode, 'error', 5000);
          this.productGrip.dataGrid.instance.focus(this.productGrip.dataGrid.instance.getCellElement(i, 'product_code'));
          return false;
        }
      }
    }
    return isValid;
  }

  xuLiApiKhiXuat() {
    this.productGrip._tinhGiaTri();
    Library.showloading();
    let xmlInvoice = "";
    if (!this.frmData.id) {
      const sub = this.billService.addNew(this.frmData, true).subscribe(res => {
        if (res.status === 'success') {
          this.frmData.bill_number = res.data.bill_number;
          this.frmData.bill_date = moment(res.data.bill_date).format('YYYY-MM-DD HH:mm:ss'); // moment(rs.data.invoice_date).format('DD/MM/YYYY');
          let bill_sign_date = moment(res.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          this.verify_code = res.data.verify_code
          this.frmData.id = res.data.id;
          xmlInvoice = res.data.xml_string;
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            Library.notify(this._translate.NOTIFY.export_success, 'success');
            this.disablleScreen();
          }
          else {
            // Ngi bn k
            if (this.configs.system_parameter.is_seller_sign_bill)
              this.xuLyKiFile(xmlInvoice, bill_sign_date);
            else {
              this.info_message = res.data.info_message;
              if (this.info_message && this.info_message.message) {
                xmlInvoice = VStringService._getValueToString(this.info_message.message.message);
                if (xmlInvoice != "") {
                  this.kyThongDiep(xmlInvoice, bill_sign_date);
                }
              }
            }
          }
        } else {
          this.confirmCheckPreview = false;
          Library.notify(res.message, 'error')
        }
      }, err => {
      }, () => {

        Library.hideloading();
        sub.unsubscribe();
      });
    }
    else {
      const sub = this.billService.edit(this.frmData.id, this.frmData, true).subscribe(res => {
        if (res.status === 'success') {
          this.frmData.bill_number = res.data.bill_number;
          this.frmData.bill_date = moment(res.data.bill_date).format('YYYY-MM-DD HH:mm:ss'); // moment(rs.data.invoice_date).format('DD/MM/YYYY');
          this.verify_code = res.data.verify_code
          let bill_sign_date = moment(res.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          this.frmData.id = res.data.id;
          xmlInvoice = res.data.xml_string;
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            Library.notify(this._translate.NOTIFY.export_success, 'success');
            this.disablleScreen();
          }
          else {
            if (this.configs.system_parameter.is_seller_sign_bill)
              this.xuLyKiFile(xmlInvoice, bill_sign_date);
            else {
              this.info_message = res.data.info_message;
              if (this.info_message && this.info_message.message) {
                xmlInvoice = VStringService._getValueToString(this.info_message.message.message);
                if (xmlInvoice != "") {
                  this.kyThongDiep(xmlInvoice, bill_sign_date);
                }
              }
            }
          }
        } else {
          this.confirmCheckPreview = false;
          Library.notify(res.message, 'error')
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }

  kyThongDiep(xmlInvoice, signDate) {
    let ListSerialNumber = "DSCKS";
    let nodeSign = "//TDiep/CKSNNT";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'thongdiep.xml', nodeSign, signDate, 'MessageData', ListSerialNumber, true);
  }

  xuLyKiFile(xmlInvoice, signDate) {
    let nodeSign = "//HDon/DSCKS/NBan";
    let ListSerialNumber = "DSCKS";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', nodeSign, signDate, 'InvoiceData', ListSerialNumber, true);

  }


  goiApiXuatHoaDon(xml_string) {
    if (this.info_message && this.info_message.message) {
      setTimeout(function () {
        Library.showloading();
      }, 20);
      const sub = this.billService.sendMessage({
        id: this.frmData.id,
        info_message: {
          message: {
            message: xml_string,
            message_code: this.info_message.message.message_code
          },
          tcntCode: this.info_message.tcntCode,
          messageCodeType: this.info_message.messageCodeType
        }
      }).subscribe(rs => {
        if (rs.status === 'success') {
          this.disablleScreen();
          // cu hnh t ng gi thng bo sai st
          if (this.configs.system_parameter.enable_send_wrong_process) {
            if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
              || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing))
              Library.notify(this._translate.NOTIFY.export_success, 'success');
            else
              this.startProcess_TBSaiSot();
          }
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }

  startProcess_TBSaiSot() {
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.getXml_TBSaiSot();
    } else {
      if (this.infoDangKy && this.infoDangKy.registration_signature) {
        let ListSerialNumber = "DSCKS";
        let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
        if (signatures && signatures.length > 0)
          ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
      }
      else
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice')

    }
  }

  getXml_TBSaiSot() {
    let bills = [];
    let object = new Object;
    object["tax_authority_code"] = this.oldForm.tax_authority_code; //M CQT cp
    object["template_code"] = this.oldForm.template_code;
    object["bill_series"] = this.oldForm.bill_series;
    object["bill_date"] = this.oldForm.bill_date;
    object["bill_number"] = this.oldForm.bill_number;
    object["reason"] = this.frmData.destroy_reason;
    object["notification_nature"] = "2"; //Tnh cht thng bo l thay th
    object["type_apply_bill"] = 1; // Loi p dng H (1: tt78)
    object["bill_id"] = this.oldForm.id;

    bills.push(object);
    let data = {
      notification_type: 1,
      bills: bills
    };
    let xmlInvoice = "";
    let sub = this.handleBillService.getXmlInvoiceWrong(data).subscribe(rs => {
      if (rs.status == 'success') {
        xmlInvoice = rs.data.xml_file;
        this.bills = rs.data.bills;
        let sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
        if (xmlInvoice != "") {
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing))
            this.sendData_TBSaiSot(xmlInvoice);
          else
            this.signXml_TBSaiSot(xmlInvoice, sign_date);
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      sub.unsubscribe();
    });
  }

  signXml_TBSaiSot(xmlInvoice, signDate) {
    let nodeSign = "//TBao/DSCKS/NNT";
    let ListSerialNumber = "DSCKS";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', nodeSign, signDate, 'NotificationData', ListSerialNumber, true);
  }


  sendData_TBSaiSot(xml_string) {
    let subXuatHoaDon = this.handleBillService.sendXmlInvoiceWrong({
      xml_file: xml_string,
      bills: this.bills
    }).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify(this._translate.NOTIFY.export_success, 'success');
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { },
      () => {
        subXuatHoaDon.unsubscribe();
      });
  }

  checkDataFormChange(dataChange, dataForm) {
    const dataBefore = JSON.parse(dataChange);
    const dataAfter = JSON.parse(JSON.stringify(dataForm));
    return _.isEqual(dataBefore, dataAfter);
  }

  onNumberChange() {
    VChanggeMoneyService._calTotalMoney(this.frmData, this.formatter, !this.frmData.auto_value);
  }

  // s kin form
  totalPaymentChange = (e) => {
    this.onNumberChange();
    this.frmData.cash = this.frmData.amount_after_vat
    if (this.frmData.auto_value) {
      this.frmData.amount_after_vat = this.frmData.total_payment;
      this.frmData.returns = this.frmData.cash - this.frmData.amount_after_vat;
    }
    let currency_code = this.frmData.currency_code;
    if (this.configs.system_parameter.view_price_nte) {
      currency_code = 'VND';
    }
    this.doctienbangchu(e.value, currency_code);
  };

  cashChange(e) {
    if (e.value) {
      this.frmData.returns = this.frmData.cash - this.frmData.amount_after_vat;
    }
  }

  onChangeSalePercen(e) {
    this.configs.system_parameter.value_rate_percent = e.value;
    let data = {
      config: {
        value: JSON.stringify(this.configs.system_parameter)
      }
    };
    this.configsService.update(this.configs.id.system_parameter, data).subscribe((response: any) => {
      if (response.status == "success") {
        this.configsService.getAll().subscribe((res) => {
          if (res.data && res.data.configs)
            this.authService.setConfigs(res.data.configs);
          this.configs = this.authService.getConfigs();
        });
      } else {
      }
    }, err => { }, () => {
    });
    if (this.productGrip)
      this.productGrip._tinhGiaTri();
  }

  preview() {
    setTimeout(() => {
      this.handleFormData();
      if (!this._validForm()) {
        return;
      }
      let data = this.billService.taodulieuXemTruocMau(this.frmData);
      data['configs'] = sEnum.cauhinhxemmau.hoadon;
      this.vPreviewInvService.xemMau(data);
    }, 300);
  }

  handleFormData() {
    if (this.frmData.bill_options) {
      this.frmData.bill_options.forEach(item => {
        delete item['is_show'];
        delete item['type'];
        delete item['width'];
        delete item['statusData'];
      });
    }
    this.frmData.adjustment_type = 5;
    this.frmData.origin_id = this.oldForm.id;
    this.frmData.total_payment = this.frmData.amount_after_vat;
    this.frmData.currency_code = 'VND';
    this.frmData.currency_rate = 1;
    // this.frmData.buyer_address = null;
    if (!this.frmData.auto_value) {
      delete this.frmData.tax_information;
    }
    if (!this.isHdGTGT) {
      delete this.frmData.tax_information;
    }   
    if (!this.frmData.tax_information)
      delete this.frmData.tax_information; 
    if(this.frmData.tax_information) {
      this.frmData.tax_information.forEach(item => {
          delete item.bill_id
          delete item.id
      })
    }
    this.deleteProperties();
    const bill_products = [];
    this.bill_products.load().then((dataSource) => {
      dataSource.forEach((item, index) => {
        delete item.error;
        delete item.price_after_vat;
        delete item.number_real_import;
        delete item.number_real_export;
        delete item.amount_without_discount;
        delete item.price_after_discount;
        if (item.product_code || item.product_name || (!item.product_name && item.is_promotion)) {
          // if (VStringService._getValueToString(this.pkInvoiceCurrent) != "")
          if ((this.frmData.adjustment_form === 'TANG' && this.frmData.adjustment_object === 'HH_DV') || (this.frmData.adjustment_form == 'GIAM' && this.frmData.adjustment_object === 'HH_DV'))
            item.row_content = this.rowContentInInvoiceProduct(item, this.frmData.adjustment_form);
          else
            item.row_content = item.product_name;
          item.id = Helper.makeid();
          bill_products.push(item);
        }
      });
      this.frmData.bill_products = bill_products;
      this.frmData["note_view_on_bill"] = `Ho n ny iu chnh cho ho n` + " " + `s ${this.oldForm.bill_number}, mu s ${this.oldForm.template_code}, k hiu ${this.oldForm.bill_series} ngy ${moment(this.oldForm.bill_date).format('DD/MM/YYYY')}`;
      this.frmData.decimal_places_json = JSON.stringify(VChanggeMoneyService.getObjFormatterToColumn(this.formatter));
    });

    this.frmData.amount = this.frmData.amount ? this.frmData.amount : 0;
    this.frmData.amount_discount = this.frmData.amount_discount ? this.frmData.amount_discount : 0;
    this.frmData.cash = this.frmData.cash ? this.frmData.cash : 0;
    this.frmData.returns = this.frmData.returns ? this.frmData.returns : 0;
    this.frmData.total_payment = this.frmData.total_payment ? this.frmData.total_payment : 0;
    this.frmData.amount_after_vat = this.frmData.amount_after_vat ? this.frmData.amount_after_vat : 0;
  }
  rowContentInInvoiceProduct(row, status) {
    // h bn hng
    let status_view = (status === 'TANG') ? 'tng' : 'gim';
    let str, temp, quantity, price;
    let bill_products = [];
    if(this.oldForm.origin) {
      bill_products = this.oldForm.origin.bill_products;
    }else{
      bill_products = this.oldForm.bill_products;
    }
    if (this.oldForm.template_code == '2') {
      bill_products.some(item => {
        if (item.product_name === row.product_name) {
          temp = item;
          return true;
        }
      });

      if (temp.quantity !== row.quantity) {
        quantity = true;
      }
      if (temp.price !== row.price) {
        price = true;
      }

      if (quantity === true || price === true) {
        str = `iu chnh ${status_view} ${quantity ? 'S lng, ' : ''}${price ? 'n gi, ' : ''}thnh tin ca mt hng`;
      } else {
        str = `iu chnh ${status_view} S lng, n gi, thnh tin ca mt hng`;
      }
      return str + ` ${row.product_name}`;
    }
    // h gtgt 
    else if (this.oldForm.template_code == '1') {
      let vat;
      bill_products.some(item => {
        if (item.product_name === row.product_name) {
          temp = item;
          return true;
        }
      });

      if (temp.quantity !== row.quantity) {
        quantity = true;
      }
      if (temp.price !== row.price) {
        price = true;
      }
      if (temp.vat !== row.vat) {
        vat = true;
      }

      let vat_view = ((temp.vat === -1 || temp.vat === 0 || !temp.vat) && vat !== true) ? '' : ', tin thu';

      if (vat) {
        str = `iu chnh ${status_view} thu sut v tin thu GTGT ca mt hng`;
      } else if (quantity === true || price === true) {
        str = `iu chnh ${status_view} ${quantity ? 'S lng, ' : ''}${price ? 'n gi, ' : ''}thnh tin${vat_view} ca mt hng`;
      } else {
        str = `iu chnh ${status_view} S lng, n gi, thnh tin, thu sut${vat_view} ca mt hng`;
      }
      return str + ` ${row.product_name}`;
    }
  }
  guideSales() {
    this.isDisplayGuideSale = !this.isDisplayGuideSale;
  }
  onTemplateTypeChange(e) {
   this.invoiceTemplateSelect = e;
   if (!this.invoiceTemplateSelect["is_discount"]) {
      this.bill_products.load().then((dataSource) => {
        dataSource.forEach((item, index) => {
          item.discount = null;
          item.amount_discount = null;
        });
      });
      this.productGrip._tinhGiaTri();
    }
    this.is_multi_vat = e.is_multi_vat;
  }
  _validForm() {
    let _isValid = true;
    const _lst = this.frmData.bill_products;
    if (!this.frmData.destroy_reason) {
      Library.notify("L do khng c  trng", 'error');
      return false;
    }
    if (!this.billService.checkVatMauHoaDon(_lst, this.is_multi_vat)) {
      return false;
    }
    return _isValid;
  }

  view() {
    let ids = [];
    ids.push(this.frmData.id);
    if (this.configs.system_parameter.view_multi_invoice) {
      let data = this.billService.taodulieuXemMau(ids);
      data['configs'] = sEnum.cauhinhxemmau.hoadon;
      this.vPreviewInvService.xemMau(data);
    }
    else {
      const initialState = {
        items: ids,
        isXuatHD: false,
        hinhthucxuat: '',
        typeAction: sEnum.typeCode.xem_hoa_don_mtt,
        cssClass: 'modal-xl view-bill'
      };
      this.vPreviewInvService.xemMauPhanTrang(initialState);
    }
  }

  deleteProperties() {
    const frmDataBill: BillModel = new BillModel(5);
    const keyOfBill = Object.keys(frmDataBill);
    const keyOfFrm = Object.keys(this.frmData);
    // const formData = this.frmData;
    for (let i = 0; i <= keyOfFrm.length; i++) {
      let error = keyOfBill.includes(keyOfFrm[i]);
      if (!error) {
        delete this.frmData[keyOfFrm[i]]
      }
    }
    delete this.frmData.total_amount_vat;

  }

  onRefreshForm() {
    this.bill_products = new ArrayStore({
      key: 'id',
      data: this.frmData.bill_products_data()
    });
    this.dataSource = new DataSource({
      store: this.bill_products
    });
    this.productGrip.createUpdateEvent();
    this.frmData.total_payment = null;
    this.frmData.amount_after_vat = null;
    this.refreshTien();
  }

  refreshTien() {
    this.frmData.amount = null;
    this.frmData.amount_vat = null;
    this.frmData.amount_after_vat = null;
    this.frmData.amount_discount = null;
    this.frmData.cash = null;
    this.frmData.returns = null;
    this.frmData.total_payment = null;
    this.frmData.total_payment_word = "";
    this.frmData.total_amount_vat = null;
  }
  setContentProductNameV2(value, type) {
    setTimeout(() => {
      if (type === 'text') {
        this.productGrip.changeColumnProductNameToText();
      } else if (type === 'select') {
        this.productGrip.changeColumnProductNameToSelect();
      }
    }, 0);
  }

  setContentProductName(value, type) {
    setTimeout(() => {
      if (this.productGrip.dataSource.items()[0]) {
        if (!this.configs.system_parameter.is_add_product_code) {
          this.productGrip.dataSource.items()[0].product_name = value;
        }
      }
      if (type === 'text') {
        this.productGrip.changeColumnProductNameToText();
      } else if (type === 'select') {
        this.productGrip.changeColumnProductNameToSelect();
      }
    }, 0);
  }

  adjustmentObjectChange(v) {
    this.doctienbangchu(this.frmData.total_payment, this.frmData.currency_code);
    let _noiDungDC = '';
    let isDC = false;
    switch (v) {
      case 'TANG_HH_DV':
        this.setContentProductNameV2('', 'select');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false, 'TANG_HH_DV');
        break;
      case 'TANG_KHAC_TANG':
        this.setContentProductName('', 'text');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false);
        break;
      case 'GIAM_HH_DV':
        this.setContentProductNameV2('', 'select');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false, 'GIAM_HH_DV');
        break;
      case 'GIAM_KHAC_GIAM':
        this.setContentProductName('', 'text');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false);
        break;
      case 'THONG_TIN_MST':
        isDC = true;
        if (this.frmData.buyer_tax_code && this.oldForm.buyer_tax_code !== this.frmData.buyer_tax_code) {
          _noiDungDC = `iu chnh m s thu Cng ty/n v mua hng t ${this.oldForm.buyer_tax_code} thnh ${this.frmData.buyer_tax_code}`;
        }
        break;
      case 'THONG_TIN_KH_DC':
        isDC = true;
        if (this.frmData.customer_name && this.oldForm.customer_name !== this.frmData.customer_name) {
          _noiDungDC = `iu chnh tn n v mua hng t ${this.oldForm.customer_name} thnh ${this.frmData.customer_name}`;
        }
        if (this.frmData.buyer_name && this.oldForm.buyer_name !== this.frmData.buyer_name) {
          if (_noiDungDC) {
            _noiDungDC += '\n';
          }
          _noiDungDC += `iu chnh tn ngi mua t ${this.oldForm.buyer_name} thnh ${this.frmData.buyer_name}`;
        }
        if (this.frmData.buyer_address && this.oldForm.buyer_address !== this.frmData.buyer_address) {
          if (_noiDungDC) {
            _noiDungDC += '\n';
          }
          _noiDungDC += `iu chnh a ch ca n v mua hng t ${this.oldForm.buyer_address} thnh ${this.frmData.buyer_address}`;
        }
        break;
      case 'THONG_TIN_TIEN_CHU':
        isDC = true;
        if (this.frmData.total_payment_word && this.oldForm.total_payment_word !== this.frmData.total_payment_word) {
          let invoice_date = moment(this.oldForm.invoice_date).format('DD/MM/YYYY');
          _noiDungDC = `iu chnh dng s tin bng ch ca ha n s ${this.oldForm.invoice_number}, k hiu ${this.oldForm.invoice_series} ngy ${invoice_date} t: ${this.oldForm.total_payment_word} thnh ${this.frmData.total_payment_word}`;
        }
        break;
      case 'KHAC_KHAC':
        setTimeout(() => {
          this.productGrip.changeColumnProductNameToText();
        }, 0);
        this.productGrip.ShowAndHidePhuongThucDC(false);
        this.productGrip.gripAllowEdit = true;
        break;
      case 'TANG_GIAM':
        this.setContentProductName('', 'text');
        this.productGrip.ShowAndHidePhuongThucDC(false);
        this.productGrip.gripAllowEdit = true;
        break;
      default:
        break;
    }
    if (isDC) {
      this.setContentProductName(_noiDungDC, 'text');
      this.productGrip.gripAllowEdit = false;
      this.productGrip.ShowAndHidePhuongThucDC(false);
    }
  }

  doctienbangchu(total_payment, currency_code) {
    if (VStringService._getValueToString(this.frmData["adjustment_object"]) == "TANG_GIAM") {
      let number_money = Math.abs(total_payment);
      let c_money = Helper.tienBangChu(number_money, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
      this.frmData.total_payment_word = c_money;
      if (total_payment < 0) {
        this.frmData.total_payment_word = "Gim " + c_money;
        if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("(", "(Adjusted to reduce by ")
        }
      }
      else if (total_payment > 0) {
        this.frmData.total_payment_word = "Tng " + c_money;
        if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("(", "(Adjusted to increase by ")
        }
      }
    }
    else {
      this.frmData.total_payment_word = Helper.tienBangChu(total_payment, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);

      if (VStringService._getValueToString(this.frmData.total_payment_word) != "" && this.frmData.total_payment_word != "Khng ng chn.") {
        if (this.frmData["adjustment_form"] === 'TANG') {
          this.frmData.total_payment_word = "Tng " + this.frmData.total_payment_word;
          if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
            this.frmData.total_payment_word = this.frmData.total_payment_word.replace("(", "(Adjusted to increase by ")
          }
        }
        if (this.frmData["adjustment_form"] == 'GIAM') {
          this.frmData.total_payment_word = "Gim " + this.frmData.total_payment_word;
          if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
            this.frmData.total_payment_word = this.frmData.total_payment_word.replace("(", "(Adjusted to reduce by ")
          }
        }
      }

    }
  }
}
