import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';

import { Library, Helper } from '@core/index';
import { BillService } from '@modules/bill-invoice/shared/bill.service';

@Injectable()
export class AdjustBillResolver implements Resolve<any[]> {
    constructor(
        private billService: BillService,
        private router: Router,
    ) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
        Library.showloading();
        let billId = route.paramMap.get('billId');
        let loaiHoaDon = route.data.type;
        return this.billService.showBillAdjustResolver(billId, loaiHoaDon, '').pipe(
            take(1),
            map(rs => {
                Library.hideloading();
                /**
                 * rs[0]: danh mc
                 * rs[1]: bill
                 * rs[2]: template
                 * rs[3]: products
                 */
                if (rs[0].status == "success" && rs[1].status == "success" && rs[2].status == "success" && rs[3].status == "success") {
                    let templates = Helper.convertTemplatesToInvoiceResolver(rs[2]);
                    return [
                        templates,
                        rs[0].data,
                        rs[1].data.bill,
                        rs[3].data.products,
                    ];
                   
                } else { // error
                    if (loaiHoaDon == "01GTKT")
                        this.router.navigate(['/system/bill-invoice']);
                    else
                        this.router.navigate(['/system/bill-sale-invoice']);

                    return null;
                }
            })
        );

    }
}
