import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './components/index/index.component';
import { ReplaceBillResolver } from './shared/replace-bill.resolver';
import { ReplaceBillComponent } from './components/replace/replace-bill/replace-bill.component';
import { AdjustBillComponent } from './components/adjust/adjust-bill/adjust-bill.component';
import { AdjustBillResolver } from './shared/adjust-bill.resolver';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent
  },
  {
    path: 'replace/:billId',
    component: ReplaceBillComponent,
    resolve: { init: ReplaceBillResolver },
    data: {
      // type: '01GTKT',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'edit-replace/:billId',
    component: ReplaceBillComponent,
    resolve: { init: ReplaceBillResolver },
    data: {
      status: 'chinhsua',
      brec: 'create_invoice_instead',
    }
  },
  {
    path: 'adjust/:billId',
    component: AdjustBillComponent,
    resolve: { init: AdjustBillResolver },
    data: {
      brec: 'create_invoice_adjustment',
      status: 'thaythemoi'
    }
  },
  {
    path: 'edit-adjust/:billId',
    component: AdjustBillComponent,
    resolve: { init: AdjustBillResolver },
    data: {
      brec: 'create_invoice_adjustment',
      status: 'chinhsua'
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HandleBillsRoutingModule { }
