import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { pattern } from '@config/pattern';
import { sEnum } from '@config/sEnum';
import { Helper, VStringService, VChanggeMoneyService } from '@core/index';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { ProductsService } from '@modules/products/shared';
import { ConfigDecimalComponent } from '@shared/index';
import { InvoiceService } from '@shared/service/invoice.service';
import { DxDataGridComponent } from 'devextreme-angular';
import CustomStore from 'devextreme/data/custom_store';
