import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';

import { Library, Helper, } from '@core/index';
import { BillService } from '@modules/bill-invoice/shared/bill.service';

@Injectable()
export class ReplaceBillResolver implements Resolve<any[]> {
  constructor(
    private billService: BillService,
    // private router: Router,
    // private authService: AuthService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    Library.showloading();
    let billId = route.paramMap.get('billId');

    return this.billService.showBillReplaceResolverTT78(billId).pipe(
      take(1),
      map(rs => {
        Library.hideloading();
        if (rs[0].status == "success" && rs[1].status == "success" && rs[2].status == "success" && rs[3].status == "success") {
          let templates = Helper.convertTemplatesToInvoiceResolver(rs[0]);
          return [
            templates,
            rs[1].data,
            rs[2].data.products,
            rs[3].data.configs,
            rs[4].data.bill,
          ];

        } else {
          // cha x l
          // if (loaiHoaDon == "01GTKT")
          //   this.router.navigate(['/system/vat-invoice']);
          // else
          //   this.router.navigate(['/system/sale-invoice']);

          return null;
        }
      })
    );

  }
}
