import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { pattern } from '@config/pattern';
import { sEnum } from '@config/sEnum';
import { VSelectBoxService, VStringService, VChanggeMoneyService, Helper } from '@core/index';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { BillService } from '@modules/bill-invoice/shared/bill.service';
import { CustomersService } from '@modules/customers/shared';
import { ChooseHdckComponent } from '@modules/handle-invoice/components/choose-hdck/choose-hdck.component';
import { AddCustomerComponent } from '@shared/components';
import { FormExtendComponent } from '@shared/components/form-extend/form-extend.component';
import { DxFormComponent } from 'devextreme-angular';
import DataSource from 'devextreme/data/data_source';
import * as moment from 'moment';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { Subscription } from 'rxjs';
import * as _ from 'lodash';

@Component({
  selector: 'app-adjust-form-bill',
  templateUrl: './adjust-form-bill.component.html',
  styleUrls: ['./adjust-form-bill.component.scss']
})
export class AdjustFormBillComponent implements OnInit, OnDestroy, AfterViewInit {
  routeSub: Subscription;
  @ViewChild(FormExtendComponent) formExtend: FormExtendComponent;
  @ViewChildren(DxFormComponent) listDxForm: any;
  @ViewChild(DxFormComponent) form: DxFormComponent;
  @Input() frmData: any;
  @Input() oldForm: any;
  @Input() bill_products: any;
  @Input() formatter: any;
  @Input() infoDangKy: any;
  @Input() _oTemplateTT78: any;
  @Input() listTemplate: any;
  @Input() isHdGTGT: boolean;
  @Input() ttDaXuatHoaDon:boolean = false;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Output('adjustmentObjectChange') adjustmentObject = new EventEmitter<any>();
  @Output('onTemplateTypeChange') onTemplateTypeChange = new EventEmitter<any>();
  @Output('onRefreshForm') onRefreshForm = new EventEmitter<any>();
  template_temp: any;
  isShow = false;
  bsModalRef: BsModalRef;
  select: any = {};
  disableForm = {
    tax_code: true,
    rest: true
  };
  pattern = pattern;
  tax_codes: any = []; //m s thu
  customer_codes: any = [];
  readOnlyCacTruongConLai: boolean;
  configs: any = {};
  _translate: any;
  emails: any = [];
  private arrEmail: any = [];
  formItems:any = [];
  customForms = []; 
  customerTempt = {};
  showPopup: boolean = false;
  customerCurent: any = {};
  tax_code_search: any;
  origin_btn_options = {
    text: '', icon: 'assets/img/icons/Xem.png', type: 'default',
    onClick: () => {
      if(this.oldForm.id) {
        // gtgt mtt
        if(this.oldForm.template_code == '1') {
          window.open(`/system/bills-invoice-vat/${this.oldForm.id}`, '_blank');
        }else if(this.oldForm.template_code == '2') {
          window.open(`/system/bills-invoice-sale/${this.oldForm.id}`, '_blank');
        }
      }

    }
  };
  mst_btn_options = {
    text: '', icon: 'assets/img/icons/Them.png', type: 'default',
    onClick: () => {
      const initialState = {
        isVisibleGhiVaThemMoi: false
      };
      this.bsModalRef = this.modalService.show(AddCustomerComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      const submodal = this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveData) {
          const customer = this.bsModalRef.content.successSaveData;
          this.frmData.customer_name = customer.name;
          this.frmData.buyer_tax_code = customer.tax_code;
          this.frmData.buyer_name = customer.contact_person_name;
          this.frmData.buyer_address = customer.business_address;
          this.frmData.customer_code = customer.code;
          this.frmData.buyer_mobile = customer.contact_mobile;

          if (customer.email) {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
            const stringDataEmailGet = customer.email.split(';');
            for (let i = 0; i < stringDataEmailGet.length; i++) {
              this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
            }
          } else {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
          }
        
        }
        submodal.unsubscribe();
      });
    }
  };

  constructor(
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private billService: BillService,
    private authService: AuthService,
    private customersService: CustomersService,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.routeSub = this.route.data.subscribe((data: any) => {
      // put data DM for select
      this.select.unit_codes = data.init[1].DM_DON_VI_TINH;
      this.select.payment_method_names = data.init[1].DM_HINH_THUC_THANH_TOAN;
      this.select.currency_codes = data.init[1].DM_LOAI_TIEN;
      this.select.customer_types = data.init[1].DM_NHOM_KHACH_HANG;
      this.select.export_places = data.init[1].DM_NOI_XUAT_HOA_DON;
      this.select.list_bank_account_seller = data.init[1].DM_TAI_KHOAN_NGAN_HANG;
      this.select.adjustment_forms = VSelectBoxService._adjustmentFormsMtt(this._translate);
      this.select.list_bank_account_seller.forEach(item => {
        item.name_display = item.code + ': ' + item.name;
      });

      this.select.templatesTT78 = this.listTemplate;
      
    });

    // tm kim m s thu
    this.tax_codes = new DataSource({
      load: (loadOptions) => {
        return this.billService.getDataSourceForm(loadOptions.searchValue, 'tax_code');
      }
    });

    // // tm kim m khch hng
    this.customer_codes = new DataSource({
      load: (loadOptions) => {
        return this.billService.getDataSourceForm(loadOptions.searchValue, 'code');
      }
    });
    this.select.adjustment_objects = [];
    if (this.frmData.adjustment_form) {
      this.adjustmentFormChange({ value: this.frmData.adjustment_form, isInit: true });
    }
    if (this.frmData.adjustment_object && this.frmData.adjustment_form) {
        this.setPropertyOfForm(this.frmData.adjustment_form, this.frmData.adjustment_object, true);
    }
    if (this.frmData.buyer_email) {
      this.emails = [];
      const emailToString = this.frmData.buyer_email;
      const a11 = emailToString.replace(/;/g, ',');
      this.emails = a11.split(',');
    }
  }

  setPropertyOfForm(adjustment_form, adjustment_object, isInit) {
    if (adjustment_form === 'TANG' && adjustment_object === 'HH_DV') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('TANG_HH_DV');
    } else if (adjustment_form === 'TANG' && adjustment_object === 'KHAC_TANG') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('TANG_KHAC_TANG');
    } else if (adjustment_form === 'GIAM' && adjustment_object === 'HH_DV') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('GIAM_HH_DV');
    } else if (adjustment_form === 'GIAM' && adjustment_object === 'KHAC_GIAM') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('GIAM_KHAC_GIAM');
    } else if (adjustment_form === 'TANG_GIAM' && adjustment_object === 'TANG_GIAM') {
      this.disableForm = { tax_code: false, rest: false };
      this.adjustmentObject.emit('TANG_GIAM');
    }
    if (adjustment_object !== 'TANG_GIAM' && !isInit) {
      this.frmData.buyer_tax_code = this.oldForm.buyer_tax_code;
      this.frmData.customer_name = this.oldForm.customer_name;
      this.frmData.buyer_address = this.oldForm.buyer_address;
      this.frmData.buyer_name = this.oldForm.buyer_name;
      this.frmData.buyer_mobile = this.oldForm.buyer_mobile;
    }
  }

  adjustmentFormChange = (e) => {
    this.disableForm = { tax_code: true, rest: true };
    let value = e.value;
    if (!e.isInit) {
      this.onRefreshForm.emit();
      value = e.itemData.code;
    }
    switch (value) {
      case 'TANG':
        if (!e.isInit)
          this.frmData.adjustment_object = '';
        this.select.adjustment_objects = [
          {
            name: 'Hng ha dch v',
            code: 'HH_DV'
          },
          {
            name: 'Khc',
            code: 'KHAC_TANG'
          },
        ];
        if (this.frmData.total_payment_word) {
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("Gim ", "Tng ");
          if (this.frmData.total_payment_word.includes('Tng') == false)
            this.frmData.total_payment_word = "Tng " + this.frmData.total_payment_word;

        }
        break;
      case 'GIAM':
        if (!e.isInit)
          this.frmData.adjustment_object = '';
        this.select.adjustment_objects = [
          {
            name: 'Hng ha dch v',
            code: 'HH_DV'
          },
          {
            name: 'Khc',
            code: 'KHAC_GIAM'
          },
        ];
        if (this.frmData.total_payment_word)
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("Tng ", "Gim ");
       
          if (this.frmData.total_payment_word && this.frmData.total_payment_word.includes('Gim') == false)
            this.frmData.total_payment_word = "Gim " + this.frmData.total_payment_word;
        
        break;
      case 'TANG_GIAM':
       
        this.select.adjustment_objects = [
          {
            name: 'iu chnh tng gim ng thi v Khc',
            code: 'TANG_GIAM'
          },
        ];
        
        this.frmData.adjustment_object = 'TANG_GIAM';
        if (this.frmData.total_payment_word) {
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("Tng ", "");
          this.frmData.total_payment_word = this.frmData.total_payment_word.replace("Gim ", "");
        }
        this.disableForm = { tax_code: false, rest: false };
        this.setPropertyOfForm(this.frmData.adjustment_form, this.frmData.adjustment_object, e.isInit);
        break;
      default:
        break;
    }
    
  };

  adjustmentObjectChange = (e) => {
    this.onRefreshForm.emit();
    this.setPropertyOfForm(this.frmData.adjustment_form, e.itemData.code, false);
  }

  onAdvancedClick() {
    this.isShow = !this.isShow;
  }

  
  createCustomGroup() {
    return {
      itemType: 'group',
      colCount: '2',
      items: this.customForms
    };
  }

  /////// s kin thay i cc trng m rng
  changeItem(_dulieumorong) {
    _dulieumorong.forEach(item => {
      for (const key in this.customerTempt) {
        if (item.code === key) {
          item.value = this.customerTempt[key];
        }
      }
      this.frmData.invoice_options = _dulieumorong;
    });
  }

  readonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = true;
    });
    this.readOnlyCacTruongConLai = true;
  }

  

  // gi data ln parent component
  onFieldDataChanged() {
    this.formEvent.emit(this.frmData);
  }

  taxCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.customer_code = customer.code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }

    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;

    this.onFieldDataChanged();
  }

  customerCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.buyer_tax_code = customer.tax_code;
    this.frmData.customer_id = customer.id;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }

    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
   

    this.onFieldDataChanged();
  }

  customerChanged = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.name) !== VStringService._getValueToString(this.frmData.customer_name)
              || VStringService._getValueToString(this.customerCurent.contact_person_name) !== VStringService._getValueToString(this.frmData.buyer_name)
              || VStringService._getValueToString(this.customerCurent.contact_mobile) !== VStringService._getValueToString(this.frmData.buyer_mobile)
              || VStringService._getValueToString(this.customerCurent.business_address) !== VStringService._getValueToString(this.frmData.buyer_address)
              || VStringService._getValueToString(this.customerCurent.email) !== VStringService._getValueToString(this.frmData.buyer_email)) {
              this.showPopup = true;
              return;
            }
          }
        }

      });

    }
  };



  onChangeTem = (e) => {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.formDuLieuMoRong(item);
        this.frmData.template_id = item.value;
        this.frmData.bill_series = item.invoice_series;
        this.frmData.template_code = item.template_code;
        this.onTemplateTypeChange.emit(item);
      }
    });
  }

  ///////////////
  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }


  paymentMethodChange = (e) => {
    const _obj = this.select.payment_method_names.filter(p => p.code === e.value)[0];
    if (_obj) {
      this.frmData.payment_method_view_name = _obj.name;
    }
  }


  capNhatDmKhachHang() {
    this.customerCurent.tax_code = VStringService._getValueToString(this.frmData.buyer_tax_code);
    this.customerCurent.name = VStringService._getValueToString(this.frmData.customer_name);
    this.customerCurent.code = VStringService._getValueToString(this.frmData.customer_code);
    this.customerCurent.contact_person_name = VStringService._getValueToString(this.frmData.buyer_name);
    this.customerCurent.contact_mobile = VStringService._getValueToString(this.frmData.buyer_mobile);
    this.customerCurent.business_address = VStringService._getValueToString(this.frmData.buyer_address);
    this.customerCurent.email = VStringService._getValueToString(this.frmData.buyer_email);
    const data = { customer: this.customerCurent };
    this.customersService.edit(data, this.customerCurent.id).subscribe((response: any) => {
      if (response.status === 'success') {
        this.showPopup = false;
      } else {
        Library.notify(response.message, 'error');
      }
    }, err => {
    }, () => {
    });

  }

  huy() {
    this.showPopup = false;
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.formEvent.emit(this.frmData);
  }
  updateCustomerInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.code) !== VStringService._getValueToString(this.frmData.customer_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  }

  updateTaxcodeInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.tax_code) !== VStringService._getValueToString(this.frmData.buyer_tax_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  }
 
  changeTaxCode(e) {
    this.tax_code_search = e.value
  }
  searchTaxCode() {
    Library.showloading();
    this.tax_code_search = this.frmData.buyer_tax_code;
    if (!this.tax_code_search) {
      Library.notify("Vui lng nhp m s thu!", "error")
    } else {
      this.billService.getInfoCompany(this.tax_code_search).subscribe(res => {
        if (res.status == "success") {
          // this.dataTaxCode = res.data.customer;
          this.frmData.customer_name = res.data.customer.name;
          this.frmData.buyer_address = res.data.customer.business_address;
        }
      })
    }
    this.onFieldDataChanged();
    setTimeout(() => {
      Library.hideloading();
    }, 1000);
  }

  disableTemplateInput() {
    if (this.frmData.bill_number) {
      return true;
    }
    return false;
  }


  formDuLieuMoRong(item) {
    let _dulieumorong = _.cloneDeep(item.fields).filter(x => x.type === '4');
    Helper.resetObject(this.formItems)
    this.customForms = [];
    this.customerTempt = {};
    const bill_options = this.frmData.bill_options;

    if (bill_options.length > 0) {
      _dulieumorong = _dulieumorong.map((dl) => {
        let option = bill_options.find(x => x.code == dl.code)
        if (option) {
          dl.value = option.value;
        }
        return dl;
      });
    }

    this.frmData.bill_options = _dulieumorong;
    if (_dulieumorong.length > 0) {
      this.formExtend.createForm(this.customForms, _dulieumorong)
    }
    _dulieumorong.forEach(value => {
      this.customerTempt[value.code] = value.value;
    });
  }
  ngAfterViewInit() {
    let template = null;   
    template = this.select.templatesTT78.find(item => item.value == this.frmData.template_id);
    this.onChangeTem(template);
  }

}
